import React, { Component } from "react";
import { Link } from 'react-router-dom';



class PastEventCards extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            eventName: props.card.eventName,
            date: props.card.date,
            time: props.card.time, 
            valence: props.card.valence, 
            arousal: props.card.arousal 
        };
      }

    render() {
        console.log('state',this.state);
        return (
            <div class="card" style="width: 18rem;">
                <div class="card-body">
                <h5 class="card-title">{this.props.card.eventName}</h5>
                <h6 class="card-subtitle mb-2 text-muted">Card subtitle</h6>
                <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                
                </div>
            </div>
        );
    }
}

export default PastEventCards;


